<template>
  <div>
    <div style="margin-bottom:10px">
      Keuntungan hari ini: <span style="font-size:24pt">{{ total_profit | toIdr }}</span>
    </div>

    <div class="table-container">
      <app-transaction-table
        :transactions="transactions"
        :loading="loading"
        :onViewBtnClicked="openTransactionDetailDialog"
      ></app-transaction-table>
    </div>

    <app-transaction-detail-dialog
      :visible="transactionDetailDialog.visible"
      :transaction="transactionDetailDialog.transaction"
      @closeDialog="transactionDetailDialog.visible = false"
    ></app-transaction-detail-dialog>

  </div>
</template>

<script>
import { dialog } from "@/responsive";
import deleteConfirmation from "@/components/dialog/DeleteConfirmation";
import { arrayToObject } from "@/utils/arrayToObject";
import TransactionTable from "@/components/Transaction/TransactionTable";
import TransactionDetailDialog from "@/components/Transaction/TransactionDetailDialog";

const formFields = ["name", "address", "phone", ["products", []]];

export default {
  components: {
    appDeleteConfirmation: deleteConfirmation,
    appTransactionTable: TransactionTable,
    appTransactionDetailDialog: TransactionDetailDialog
  },
  data() {
    return {
      total_profit: 0,
      transactions: [],
      products: [],
      loading: false,
      textMap: {
        update: "Edit",
        create: "Create"
      },

      // Responsive
      windowWidth: window.innerWidth,

      // Transaction Dialog
      transactionDetailDialog: {
        visible: false,
        transaction: {}
      }
    };
  },

  methods: {
    // Get
    getReportDaily() {
      this.loading = true;
      this.$store
        .dispatch("report/GenerateReportDaily")
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;

          console.log("FETCH REPORT DAILY: ", data);

          const report = data.data.report;
          this.total_profit = data.data.report.total_profit;
          this.transactions = data.data.report.transactions;
          this.loading = false;
        });
    },

    // Responsive
    makeDialogResponsive(sm = dialog.sm, md = dialog.md) {
      if (this.windowWidth < 990) {
        this.formDialog = {
          ...this.formDialog,
          dialog: sm
        };
      } else {
        this.formDialog = {
          ...this.formDialog,
          dialog: md
        };
      }
    },

    // Transaction dialog
    openTransactionDetailDialog(transactionId) {
      console.log("Open transaction detail dialog");
      this.transactionDetailDialog = {
        ...this.transactionDetailDialog,
        transaction: this.transactions.filter(transaction => {
          return transaction.transaction_id === transactionId;
        })[0]
      };
      this.transactionDetailDialog.visible = true;
      console.log("transaction => ", this.transactionDetailDialog.transaction);
    },
    closeTransactionDetailDialog() {
      this.transactionDetailDialog.visible = false;
    }
  },

  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    }
  },

  created() {
    // this.getTransactions();
    this.getReportDaily();
  },

  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener("resize", function(e) {
        that.windowWidth = window.innerWidth;
      });
    });
  }
};
</script>



